/**
 * @description node polyfill
 * @author wangfupeng
 */

// @ts-nocheck

if (typeof global === 'object') {
  //  nodejs 
  const globalProperty = Object.getOwnPropertyDescriptor(global, 'window')

  // global.window 
  // global.window
  if (!global.window || globalProperty.set) {
    global.window = global
    global.requestAnimationFrame = () => {}
    global.navigator = {
      userAgent: '',
    }
    global.location = {
      hostname: '0.0.0.0',
      port: 0,
      protocol: 'http:',
    }
    global.btoa = () => {}
  }
}
